body {
  color-scheme: light;
  --text-color: #000;
  --bg-color: #fff;
  --highlight-color: #bdf5ff;
  --restart-color: #06b432;
  margin: .5rem;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
}

body.dark-theme {
  color-scheme: dark;
  --text-color: #eee;
  --bg-color: #121212;
  --highlight-color: #1d4850;
  --restart-color: #daa520;
}

@media (prefers-color-scheme: dark) {
  body.light-theme {
    color-scheme: light;
    --text-color: #000;
    --bg-color: #fff;
    --highlight-color: #bdf5ff;
    --restart-color: #06b432;
  }

  body {
    color-scheme: dark;
    --text-color: #eee;
    --bg-color: #121212;
    --highlight-color: #1d4850;
    --restart-color: #daa520;
  }
}

body {
  color: var(--text-color);
  background-color: var(--bg-color);
}

.main {
  height: calc(100vh - 1rem);
  flex-direction: row;
  display: flex;
}

.main hr {
  margin: 0 1rem;
}

.left {
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

#settings {
  grid-template-columns: max-content max-content;
  align-items: center;
  gap: .5rem;
  display: grid;
}

#settings label {
  text-align: right;
}

#settings #timeTable {
  width: 40em;
  height: 16em;
}

#settings #onlyRequiredRestarts {
  justify-self: start;
}

#settings #solveBtn {
  grid-column: 1 / 3;
  margin-top: 1rem;
}

.infos {
  justify-content: space-between;
  align-items: end;
  display: flex;
}

.links {
  flex-direction: column;
  display: flex;
}

.links a {
  color: #6969c2;
  text-decoration: none;
}

.output {
  width: 100%;
  margin-left: 2rem;
  position: relative;
}

.restart {
  color: var(--restart-color);
  font-weight: bolder;
}

.rightFooter {
  position: absolute;
  bottom: 0;
  right: 0;
}

#outputList {
  padding: 0;
  list-style-type: decimal;
}

#outputList :not(:first-child) {
  margin-top: .5rem;
}

#outputList {
  width: -moz-fit-content;
  width: fit-content;
}

#outputList li code {
  font-size: large;
}

#errorMessage {
  color: red;
}

.spinner {
  width: 16px;
  height: 16px;
  border: 2px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  animation: 2s linear infinite spin;
}

.disabled {
  display: none;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.newSolution {
  border-radius: 3px;
  animation: .75s ease-out highlight;
}

@media (prefers-reduced-motion) {
  .newSolution {
    animation: none;
  }
}

@keyframes highlight {
  0% {
    background-color: var(--highlight-color);
  }

  100% {
    background-color: var(--background-color);
  }
}

@media only screen and (max-width: 1600px) {
  .left {
    max-width: 50%;
  }

  #settings {
    grid-template-columns: 1fr 2fr;
  }

  #settings #timeTable {
    width: auto;
  }
}

@media only screen and (max-width: 1020px) {
  .main {
    flex-direction: column;
  }

  .left {
    max-width: 100%;
  }

  #settings {
    width: 100%;
    grid-template-columns: max-content 1fr;
  }

  #settings #timeTable {
    width: auto;
  }

  .infos {
    display: none;
  }

  .main hr {
    margin: 1rem 0;
  }

  .rightFooter {
    display: none;
  }
}

@media only screen and (max-width: 600px) {
  #settings {
    grid-template-columns: 1fr 3fr;
  }
}

